import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useParams } from "react-router-dom";
import { TAGGSHOP_SOCIAL_ACCOUNT_DATA } from "../../../../actions/sceneActions";
import { addAccountFromId } from "../../../../utils/addAccount";
import { Loader } from "../CommonComponents/Loader";
import SocialAccount from "./SocialAccount";

const controller = new AbortController();
const channel = new BroadcastChannel('connect');
const ShareAccount = () => {
    const params = useParams();
    const dispatch = useDispatch();
    const [loading,setLoading] = useState(true);

    const handleMessage = async(event) => {
        try {
            const response = JSON.parse(event.data);
            const { code } = response;
            const networkId = localStorage.getItem('networkId');
            const type = localStorage.getItem('connectionType');
            if (event.origin.includes("tagshop.ai") || event.origin.includes("localhost")) {
                if(type == 'connectAccount'){
                    if (code) await addAccountFromId(parseInt(networkId), code, () => dispatch(TAGGSHOP_SOCIAL_ACCOUNT_DATA(params.id, setLoading)), true);
                }
            }
        } catch (error) {
            
        }
    };

    useEffect(() => {
        const signal = controller.signal;
        window.addEventListener("message", handleMessage, { signal });
        channel.addEventListener("message", handleMessage);
        dispatch(TAGGSHOP_SOCIAL_ACCOUNT_DATA(params.id, setLoading))
        localStorage.setItem('shared_token', params.id)
        localStorage.setItem('ownerId', params.id)
        localStorage.setItem('shareAccount', true)
        localStorage.setItem('url_redirect', window.location.pathname.replace('/v2', ''))
        return () => {
            window.removeEventListener("message", handleMessage, { signal });
            channel.removeEventListener("message", handleMessage);
            controller.abort();
            channel.close();
        }
    }, [])

    return loading ? <Loader/> : <SocialAccount isSharing={true}/>
    
}

export default ShareAccount